
import * as Styled from './styles';
import { Heading } from '../Heading';
import Link from 'next/link';

export type HeaderProps = {
  title?: string;
  content?:string;
  srcImg?: string;
  link: string;
  altText: string;
  newTab: boolean;
};

export const Header = ({
  title,
  content='',
  srcImg = '',
  link,
  altText,
  newTab = false,

}: HeaderProps) => {
  const target = newTab ? '_blank' : '_self';

  if (title) {
    return (

          <Styled.Container target={target}>
            {!!srcImg && <img src={srcImg} alt={altText} />}
            <Heading size="big" uppercase>
            {title}
            </Heading>
            <HtmlContent html={content} />
          </Styled.Container>

    );
  }

  return (
    <Heading size="small" uppercase>
      <Styled.Container target={target}>
        {!!srcImg && <img src={srcImg} alt={altText} />}
      </Styled.Container>
    </Heading>
  );
};
